.keypad{
	flex:1.2;
	background-color: var(--bg-color-2);
	padding: 20px;
	display: flex;
}

.keypad p{
	line-height: 1.875rem;
	font-size: 1.8rem;
	color: var(--text-color-2);
	display: flex;
	justify-content: center;
	align-items: center;
	transition: 200ms;
	cursor: pointer;
}

.keypad_keys p:active{
	background-color: rgba(0,0,0,0.14);
	color: black;
}

.keypad_keys{
	flex: 3;
	display: flex;
	flex-wrap: wrap;
}

.keypad_keys p{
	width: 33%;
	height: 25%;
}

.keypad_symbols{
	flex: 1;
}

.keypad_symbols p{
	width: 100%;
	height: 20%;
	color: #89a5f1;
}

.keypad_symbols p:active{
	background-color: rgba(0,0,0,0.14);
	color: black;
}
