.header{
	flex:1;
	background-color: var(--bg-color-1);
	padding: 20px;
	overflow-y: scroll;
}

.header p{
	font-size: 1rem;
	line-height: 1.875rem;
	color: var(--text-color-1);
	display: flex;
	justify-content: flex-end;
}

.header_expression{
	overflow-x: scroll;
	width: 100%;
	min-height: 30px;
}

.header_expression p{
	white-space: nowrap;
	width: fit-content;
	min-width: 100%;
	font-weight: bold;
}

.header_result{
	font-weight: bold;
	font-size: 1.4rem !important;
	min-height: 30px;
}